export default {
  mounted(el, binding) {
    const modifiers = binding.modifiers;
    const { values, value, callback } = binding.value;
    let count = 0;
    // å¾ªç¯å¹é
    values.forEach((item) => {
      if (item == value) {
        count++;
      }
    });
    if (count == 0) {
      // v-show åç¬éèå±æ§ v-ifç´æ¥å é¤å±æ§  é»è®¤v-if
      if (modifiers["v-show"]) {
        el.style.display = "none";
      } else if (modifiers["v-if"]) {
        el.remove();
      } else {
        el.remove();
      }
    }
    if (callback) callback(count);
  },
};
